    socket.on(
      "payment",
      ({ type, pornCoins, amount, senderId, targetId, senderName }) => {
        socket.emit("updateData");

        sock.log("payment", {
          type,
          pornCoins,
          amount,
          senderId,
          targetId,
          senderName,
        });
        setPornCoins(pornCoins);

        if (type === "out") {
          openAlert("Перевод успешно выполнен!");
          setActiveModal(null);
        } else if (type === "in") {
          openAlert(
            <span>
              <a href={`https://vk.com/id${senderId}`} target="_blank">
                {senderName}
              </a>
              {` отправил Вам ${formatCoins(amount)} PornCoins`}
            </span>
          );
        }
      }
    );

