  // type === "vip"
  // 	? `vip ${choose} ${period} (не менять)`
  // 	: `gold ${choose} ${period} (не менять)`,

  function qiwiPayVIP(type) {
    window.QiwiCheckout.createInvoice({
      publicKey: QIWI_PUBLIC_KEY,
      amount:
        type === "Бронзовый"
          ? 9
          : type === "Серебряный"
          ? 19
          : type === "Золотой"
          ? 39
          : type === "Платиновый"
          ? 69
          : "",
      comment:
        type === "Бронзовый"
          ? `bronze (не менять)`
          : type === "Серебряный"
          ? `silver (не менять)`
          : type === "Золотой"
          ? `gold (не менять)`
          : type === "Платиновый"
          ? `platinum (не менять)`
          : "",
    })
      .then((response) => {
        console.log("qiwi vip", response);
      })
      .catch((error) => {
        console.log("QIWI VIP ERROR:", error);
      });
  }

  function withdraw() {
    if (!amount || Number.isNaN(+amount)) {
      setActiveModal(null);
      return openAlert("Сумма должна быть числом", "red");
    }

    if (amount > rubles) {
      setActiveModal(null);
      return openAlert("Не хватает средств для вывода", "red");
    }

    if (amount < 10) {
      setActiveModal(null);
      return openAlert("Минимальная сумма вывода 10 руб.", "red");
    }

    if (!qiwiAccount.startsWith("+7")) {
      setActiveModal(null);
      return openAlert("Номер кошелька должен начинаться с +7", "red");
    }

    callMethod("withdraw", {
      qiwiAccount,
      amount,
    }).then(({ status, message }) => {
      setActiveModal(null);
      openAlert(message, status === "denied" ? "red" : "green");
    });
  }

  function exchangeQiwiPay() {
    if (!amount || Number.isNaN(+amount) || amount < 1) {
      setActiveModal(null);
      return openAlert("Сумма должна быть целым положительным числом", "red");
    }

    window.QiwiCheckout.createInvoice({
      amount,
      publicKey: QIWI_PUBLIC_KEY,
      comment: `withdraw ${USER_ID}`,
    })
      .then((res) => {
        console.log("qiwi", res);
      })
      .catch((err) => {
        console.log("QIWI ERROR:", err);
      });
  }

