  function onChangeHandlerId(e) {
    const value = e.target.value.trim();
    targetId = value;

    setIdUserInventory(value);
    console.log("onChangeHandlerId targetId", targetId);
    console.log("onChangeHandlerId idUserInventory", targetId);
  }

  function onChangeHandlerAmount(e) {
    const value = e.target.value.trim();
    amount = +value;

    if (value < 0) {
      amount = +value;
    }

    const element = document.querySelector("#comission");

    if (element) {
      element.innerHTML = `Комиссия PornCoin ${(amount * 0.1).toFixed(
        2
      )} руб.</br>К оплате: ${(amount * 1.1).toFixed(2)} руб.`;
    }
  }

  function onChangeHandlerPayment(e) {
    const value = e.target.value.trim();
    amount = +e.target.value;

    if (e.target.value * course < 1) {
      let needCoins = 0;

      while (needCoins * course < 1) {
        needCoins++;
      }

      document.querySelector(
        "#toPay"
      ).innerHTML = `Минимальное кол-во ${needCoins} PornCoins`;
    } else {
      document.querySelector("#toPay").innerHTML = `К оплате: ${(
        e.target.value * course
      ).toFixed(2)} руб`;
    }
  }

